

























































import { Component, Vue } from 'vue-property-decorator';
import ActionBlock from '@/components/search/actionBlock.vue'
import AddContact from '@/views/alarm/add_contact.vue'
import AddToGroup from '@/views/alarm/add_to_group.vue'
import Service from '@/https/alarm/list'
import CommonDialog from '@/views/alarm/commonDialog.vue'
import {Table} from'element-ui'
import {rightClick} from "@/utils/vmOp2/rightClick";
import RightClick from "@/views/vmOp2/component/right-click.vue";
import {hideMenu} from "@/utils/vmOp2/hideMenu";
@Component({
    components:{
      RightClick,
        ActionBlock,
        AddContact,
        AddToGroup,
        CommonDialog
    }
})
export default class ContactList extends Vue{
    $route;
    private type_list=[
        {
            type:'name',
            label:'姓名'
        },
        {
            type:'email',
            label:'邮箱'
        },
        {
            type:'phone',
            label:'电话号码'
        },
    ]
    private search={
        type:{placeholder:'请输入后查询',list:this.type_list,type:'composite',width:340},
    }
    private list = []
    private current:number=1
    private size:number=20
    private total:number=0
    private user_visible:Boolean=false
    private user_id:String=""
    private user_title:String="新建联系人"
    private group_visible:Boolean=false;
    private del_visible:Boolean=false
    private contact_rows:any=[]
    private search_data:any={}
    private auth_list:any=[]
  private menus= [
    {label: '编辑',value: 'edit_contact',single:true,disabled:false},
    {label: '删除联系人',value: 'delete_contact',disabled:false},
    {label: '添加至报警联系组',value: 'add_to_group',disabled:false},
  ]
    created() {
        this.fn_search()
        this.auth_list=this.$store.state.auth_info['alarm_contact']
    }
  //改变点击行得选中状态
  private FnOperRow(row){
    (this.$refs.contact_list as any).toggleRowSelection(row)
  }
  //改变选中行的背景颜色
  rowStyle({row}) {
    const isSelected = this.contact_rows.some(item => item.id === row.id);
    if (isSelected) {
      return 'rowStyle'
    }
  }
  //右键弹出操作
  FnRightClick(row,column,event){
    //判断当前行是否被选中，没选中时需选中并弹出菜单
    const isSelected = this.contact_rows.some(item => item.id === row.id);
    if (!isSelected) {
      (this.$refs.contact_list as any).toggleRowSelection(row)
    }
    rightClick(row,column,event)
  }
  private handle(item) {
    const { label, value } = item;
    if(value === 'edit_contact'){
      this.edit(this.contact_rows[0].id)
    }else if(value === 'delete_contact'){
      this.contact_rows.length >=1 ? this.del({}) : this.del(this.contact_rows[0])
    } else {
      this.addToWarnGroup()
    }
    hideMenu()
  }
    private fn_search(data:any={}){
        this.current = 1
        this.search_data = data
        this.getContactList()
    }
    private async getContactList(){
        let res:any = await Service.get_contact_list({
            page:this.current,
            pageSize:this.size,
            [this.search_data.typesub ? this.search_data.typesub : 'name']:this.search_data.type
        })
        if(res.code==='Success'){
            this.list = res.data.datas
            this.list.map(item=>{
                let arr = item.groups.map(inn=>inn.name)
                item.groupName = arr.join(',')
                return item;
            })
            this.total = res.data.total || 0
        }
    }
    private handleSizeChange(size){
        this.size=size
        this.getContactList()
    }
    private handleCurrentChange(cur){
        this.current = cur
        this.getContactList()
    }
    private handleSelectionChange(val){
        this.contact_rows = val

    }
    private add(){
        this.user_title="新建联系人"
        this.user_visible=true
    }
    private async edit(id:string){
        this.user_id=id
        this.user_title="编辑联系人"
        this.user_visible=true
        // let res:any = await Service.get_contact_detail({
        //     id,
        // })
        // if(res.code===0){
        //     this.user_visible=true
        // }else{

        // }
    }
    private del(id){
        if(Object.keys(id).length>0){
            this.contact_rows=[id]
        }
        if(this.contact_rows.length===0){
            this.$message.warning("请勾选联系人！")
            return;
        }

        this.del_visible = true

    }
    private close(val){
        this.user_visible=false
        this.group_visible=false
        this.del_visible=false
        this.contact_rows=[]
        this.user_id=""
        val==='1'&& this.getContactList()
        const table = this.$refs.contact_list as Table
        table.clearSelection()

    }
    private addToWarnGroup(){
        if(this.contact_rows.length===0){
            this.$message.warning("请先勾选联系人！");
            return;
        }
        this.group_visible=true
    }

}
